.contacts {
    .title {
        color: $white;
    }

    .contacts-second {
        display: flex;
        flex-direction: column;
        justify-content: space-between;
    }

    .col-50 {
        margin-right: 5%;


        @include screen(740) {
            width: 100%;
            margin: 3% 0;
        }
    }

    &__form {

            &-item {
                border: 1px solid rgba(255,255,255,0.5);
                height: 48px;
                padding: 15px 20px;
                outline: none;
                font-size: 12px;
                width: 100%;
                background: transparent;
                color: rgba(255,255,255,0.8);
                margin-top: 20px; 

                &:focus {
                    border-color: $white;
                    color: $white;
                }
            }

            textarea {
                height: 15vh;
            }
            
    }

    &__form-bottom {
        display: flex;
        justify-content: space-between;
        padding: 0 4%;
        align-items: center;

        .button {
            text-align: center;
            transition: .5s;
            max-width: 200px;
            font-size: 16px;

            &:hover {
                background: #ff2b4a;
                border-color: #ff2b4a;
                color: $white;
            }
        }
    }

    &__address {
        padding: 30px 25px;

        @include screen(740) {
            align-items: center;
            margin: 5px;
        }
    }

    &__phone {
        padding: 30px 25px;

        @include screen(740) {
            align-items: center;
            margin: 5px;
        }
    }

    &__web {
        padding: 30px 25px;

        @include screen(740) {
            align-items: center;
            margin: 5px;
        }
    }
}